$site-header-padding: 0;


//
//  COLOR
//––––––––––––––––––––––––––––––––––––––––––––––––––

$site-header-bg-color: $color-primary;


//
//  STYLES
//––––––––––––––––––––––––––––––––––––––––––––––––––

