//
//  COLOR
//––––––––––––––––––––––––––––––––––––––––––––––––––


// Basics
$black: #000;
$white: #fff;


// Utility colors
$color-primary: #284c71;
// $color-primary-darker: #212f3d;
// $color-primary-lighter: #23609e;
$color-primary-darker: darken($color-primary, 5%);
$color-primary-lighter: lighten($color-primary, 5%);

$color-secondary: #ff851b;


// Grays
$gray-mixer: $color-primary;
$color-gray-e: mix(#eee, $gray-mixer, 99%);
$color-gray-d: mix(#ddd, $gray-mixer, 98%);
$color-gray-c: mix(#ccc, $gray-mixer, 97%);
$color-gray-b: mix(#bbb, $gray-mixer, 97%);
$color-gray-a: mix(#aaa, $gray-mixer, 95%);
$color-gray-9: mix(#999, $gray-mixer, 94%);
$color-gray-8: mix(#888, $gray-mixer, 94%);
$color-gray-7: mix(#777, $gray-mixer, 93%);
$color-gray-6: mix(#666, $gray-mixer, 92%);
$color-gray-5: mix(#555, $gray-mixer, 91%);
$color-gray-4: mix(#444, $gray-mixer, 90%);
$color-gray-3: mix(#333, $gray-mixer, 89%);
$color-gray-2: mix(#222, $gray-mixer, 88%);
$color-gray-1: mix(#111, $gray-mixer, 85%);
